/* @import url(https://fonts.googleapis.com/css?family=PlayfairDisplay); */
* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  background: url('http://farm4.staticflickr.com/3485/3288052387_716d3696d8_o.jpg')
    no-repeat;
  background-size: 100%;
  background-position: top center;
  margin: 0;
  padding: 0;
  background-attachment: fixed;
  font-size: 20px;
  line-height: 32px;
  color: #333333;
  font-family: Playfair Display, Serif;
}

.container {
  margin: 5% auto;
}

.main-section {
  color: gray;
  text-emphasis-color: white;
}

.search,
.search ul,
.search li,
.search button,
.search input {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  background: transparent;
}

.search {
  max-width: 524px;
  margin: 0 auto;
}

.search button,
.search input {
  font: 18px/1.25em Helvetica, Arial, Sans-serif;
}
.search-header {
  display: inline-flex;
  background: #343e50;
  justify-content: space-between;
  width: 100%;
  border-radius: 100px;
}
.search-header input,
.search-header button {
  color: #fff;
  box-sizing: border-box;
  font-size: 1.25em;
  padding: 14px;
}
.search-header input {
  flex-grow: 2;
}

.search-header button {
  background: #1dd2af;
  border-left: 1px solid #1eddb8;
  border-radius: 2em;
}

.search-header button:hover,
.cel-to-fah button:hover {
  background: #20e7c1;
}

h1,
h2 {
  font-size: 40px;
  text-align: center;
  font-family: helvetica, sans-serif;
  text-transform: uppercase;
  color: #ffffff;
  margin: 25% 0;
}
@media (max-width: 800px) {
  h1,
  h2 {
    font-size: 30px;
  }
}

@media (max-width: 550px) {
  .search-header input,
  .search-header button {
    font-size: 1em;
  }
}

a {
  text-decoration: none;
  color: #34495e;
}
a:hover {
  color: #2c3e50;
  text-decoration: underline;
}

img {
  width: 100%;
}

.wrapper {
  background: #ffffff;
  width: 100%;
  min-height: 100%;
  margin: 0 0 0 0;
  position: relative;
}

.content-wrapper {
  width: 80%;
  margin: auto;
  padding: 10%;
}
@media (max-width: 640px) {
  .content-wrapper {
    width: 100%;
    padding: 10% 2%;
  }
}
.content-wrapper:first-letter {
  font-size: 80px;
}
.content-wrapper .image-right {
  padding: 5% 0 5% 5%;
  width: 50%;
  float: right;
  opacity: 0.8;
  -moz-transition-property: all;
  -o-transition-property: all;
  -webkit-transition-property: all;
  transition-property: all;
  -moz-transition-duration: 0.6s;
  -o-transition-duration: 0.6s;
  -webkit-transition-duration: 0.6s;
  transition-duration: 0.6s;
  -moz-transition-timing-function: ease-in;
  -o-transition-timing-function: ease-in;
  -webkit-transition-timing-function: ease-in;
  transition-timing-function: ease-in;
  font-size: 80%;
  font-style: italic;
  line-height: 20px;
}
@media (max-width: 900px) {
  .content-wrapper .image-right {
    width: 100%;
    float: left;
    padding: 0 0 5% 0;
  }
}
.content-wrapper .image-right img {
  margin: 0 0 10px 0;
}
.content-wrapper .image-right:hover {
  opacity: 1;
}
.content-wrapper .image-left {
  padding: 5% 5% 5% 0;
  width: 50%;
  float: left;
  opacity: 0.8;
  -moz-transition-property: all;
  -o-transition-property: all;
  -webkit-transition-property: all;
  transition-property: all;
  -moz-transition-duration: 0.6s;
  -o-transition-duration: 0.6s;
  -webkit-transition-duration: 0.6s;
  transition-duration: 0.6s;
  -moz-transition-timing-function: ease-in;
  -o-transition-timing-function: ease-in;
  -webkit-transition-timing-function: ease-in;
  transition-timing-function: ease-in;
  font-size: 80%;
  font-style: italic;
  line-height: 20px;
}
@media (max-width: 900px) {
  .content-wrapper .image-left {
    width: 100%;
    padding: 0 0 5% 0;
  }
}
.content-wrapper .image-left img {
  margin: 0 0 10px 0;
}
.content-wrapper .image-left:hover {
  opacity: 1;
}
.content-wrapper .image-center {
  padding: 5% 0;
  width: 169%;
  position: relative;
  margin-left: -35%;
}
@media (max-width: 640px) {
  .content-wrapper .image-center {
    width: 127%;
    margin-left: -13%;
  }
}
